
 .box {
    width: 300px;
    margin: auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    background-color: #fff;

  }
  
  .error-message {
    color: #ff0000;
    margin-bottom: 10px;
    font-weight: 700;
  }
  
  form {
    display: flex;
    flex-direction: column;
  }
  
  label {
    margin-bottom: 5px;
  }
  
  input {
    padding: 8px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  button {
    padding: 10px;
    background-color: #4caf50;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #45a049;
  }

  
.button-container {
    text-align: center;
    margin-top: 50px;
  }
  
  .login-button,
  .signup-button {
    padding: 10px 20px;
    font-size: 16px;
    margin: 0 10px;
    cursor: pointer;
    border: none;
    border-radius: 4px;
  }
  
  .login-button {
    background-color: #4caf50;
    color: #fff;
  }
  
  .login-button:hover {
    background-color: #45a049;
  }
  
  .signup-button {
    background-color: #2196f3;
    color: #fff;
  }
  
  .signup-button:hover {
    background-color: #1e87da;
  }